<template>
  <span v-loading='loading'>
    <el-cascader v-model="area" :props='props' placeholder="请选择地区" @change='change' :options="allData" :filterable='filterable' :clearable='clearable'></el-cascader>
  </span>
</template>

<script>
export default {
  data() {
    return {
      allData: [],
      loading: true,
      area: [],
      props: {
        value: "area_id",
        label: "area_name",
        children: "son",
      },
    };
  },
  props: {
    filterable: {
      type: Boolean,
      default: false,
    },
    clearable: {
      type: Boolean,
      default: true,
    },
    defaultArea: {
      type: Array,
      default: () => [],
    },
  },
  methods: {
    change(val) {
      this.$emit("change", val);
    },
    getData() {
      this.$axios({
        method: "get",
        url: "v2/open/area/getAreaList",
        params: {
          is_area: 1,
        },
      }).then(({ data }) => {
        if (data.code == 200) {
          this.allData = data.data;
          this.loading = false;
          this.area = this.defaultArea
        }
      });
    },
  },
  created() {
    this.getData();
  },
};
</script>

