<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item>客户管理</el-breadcrumb-item>
      <el-breadcrumb-item class="attrbute">
        <span @click="goBack">客户资料</span>
      </el-breadcrumb-item>
      <el-breadcrumb-item>{{title}}客户资料</el-breadcrumb-item>
    </el-breadcrumb>
    <div class="">
      <el-form>
        <el-form-item label="客户类型：">
          <el-radio-group v-model="params.customer_genre" @change="radioChange">
            <el-radio :label='0'>零售商</el-radio>
            <el-radio :label='1'>连锁门店</el-radio>
            <el-radio :label='2' v-if='is_service==1'>服务站</el-radio>
          </el-radio-group>
        </el-form-item>
      </el-form>
      <div class="">
        <div class="paymanageTitle">
          <i></i>
          <span>客户基本信息</span>
        </div>
        <el-form :inline="true" :model="params" ref="params" class="demo-form-inline">
          <el-form-item label="销售组织" size="mini" prop="company_id" :rules="params.internal_company_id==0?rules.company_id:null">
            <erpCompanyList ref='erpCompanyList' @change="business" v-model="params.company_id" :disabled="this.customerinfoAdd_showGet.customer_id!=null" @getList='getCompanyList' />
          </el-form-item>
          <el-form-item label="客户编码">
            <el-input v-model="params.customer_id" disabled size="mini"></el-input>
          </el-form-item>
          <el-form-item label="客户名称" prop="customer_name" :rules="[{ required: true, message: '请输入客户名称', trigger: 'blur' }]">
            <el-input v-model="params.customer_name" placeholder="请输入" maxlength="25" size="mini" @change="customerNameBlur"></el-input>
          </el-form-item>
          <el-form-item label="助记码" prop="logogram" :rules="[{required: true, message: '请输入助记码', trigger: 'change'}]">
            <el-input v-model="params.logogram" maxlength="50" size="mini" placeholder="输入客户名称自动生成"></el-input>
          </el-form-item>
          <el-form-item label="客户分类" size="mini" prop="customer_type_id" :rules="[{ required: true, message: '请选择客户分类', trigger: 'change' }]">
            {{(function(){if(params.customer_type_id == 0)params.customer_type_id = null})()}}
            <el-select clearable v-model="params.customer_type_id" placeholder="请选择" @focus="getCustomerTypeFoucs" size="mini" filterable remote :remote-method="customerSearch">
              <el-option :label="item.customer_type_name" :value="item.customer_type_id" :key="item.customer_type_id" v-for="item in customerType.options"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="联系人">
            <el-input v-model="params.contact_name" maxlength="50" placeholder="输入客户名称自动生成" size="mini"></el-input>
          </el-form-item>
          <el-form-item label="手机号码" prop="customer_mobile" :rules="params.customer_genre==2?[{required: true, message: '请输入手机号码', trigger: 'change'}]:null">
            <el-input v-model="params.customer_mobile" size="mini" placeholder="请输入"></el-input>
          </el-form-item>
          <el-form-item label="所属区域" size="mini" prop="cityList" :rules="rules.areaadress">
            <p>
              <getAreaList :filterable='true' @change="changeDefaultArea" :defaultArea="defaultArea" />
            </p>
          </el-form-item>
          <el-form-item label="详细地址" prop="address" :rules="[{ required: true, message: '请输入详细地址', trigger: 'blur' }]" size="mini">
            <el-input size="mini" type="text" v-model="params.address" placeholder="请输入" suffix-icon="el-icon-edit-outline" :maxlength="300"></el-input>
          </el-form-item>

          <el-form-item label="业务员" size="mini">
            {{(function(){if(params.business_id == 0)params.business_id = null})()}}
            <el-select v-model="params.business_id" @focus="getbusinIdFoucs" filterable size="mini" placeholder="请选择" clearable>
              <el-option :label="item.employee_name" :value="item.employee_id" :key="item.employee_id" v-for="item in employeeList.options"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="电话号码">
            <el-input v-model="params.customer_phone" placeholder="请输入" size="mini"></el-input>
          </el-form-item>
          <el-form-item label="备用电话">
            <el-input v-model="params.bak_phone" placeholder="请输入" size="mini"></el-input>
          </el-form-item>
          <el-form-item label="身份证号" prop='id_card' :rules="rules.id_card2">
            <el-input v-model="params.id_card" size="mini" placeholder="请输入"></el-input>
          </el-form-item>
          <el-form-item label="转内部客户">
            <el-select v-model="params.internal_company_id" @focus="getv2getInternalCompanyList" multiple collapse-tags placeholder="请选择">
              <el-option v-for="item in InternalCompanyList" :disabled="item.is_already" :key="item.company_id" :label="item.company_name" :value="item.company_id">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="是否停用" prop="is_disabled" :rules="[{ required: true, message: '请选择是否停用', trigger: 'change' }]">
            <el-radio-group v-model="params.is_disabled" size="mini">
              <el-radio :label="1">&nbsp;是</el-radio>
              <el-radio :label="0">&nbsp;否</el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="账龄期限(天)" prop='overdue_period' :rules="[{ required: true, message: '请输入账龄期限', trigger: 'change' }]">
            <el-input v-model="params.overdue_period" size="mini" type="number" v-integerNumber placeholder="请输入"></el-input>
          </el-form-item>
          <el-form-item label="备注">
            <el-input v-model="params.remark" size="mini" placeholder="请输入"></el-input>
          </el-form-item>
        </el-form>
        <div class="specialBorderDiv">
          <p>注意</p>
          <div><span>手机号码：</span>和电商打通的企业，建议最好输入客户手机号码，因为电商推送到ERP的单据，是以客户手机号匹配的，若匹配不到，ERP会自动创建以电商手机号为准的新客户；<br />
            <div>非和电商打通的企业，客户手机号可为空；</div>
          </div>
        </div>
        <div class="refundauditFoot">
          <span v-if="!this.customerinfoAdd_showGet.customer_id">创建人：{{employee_name}}</span>
          <span v-else>创建人：{{this.params.add_user_name}}</span>
          <span>创建时间：{{this.params.add_time||""}}</span>
          <span>修改人：{{this.params.update_user_name||""}}</span>
          <span>修改时间：{{this.params.update_time||""}}</span>
        </div>
      </div>
    </div>
    <div slot="footer" class="dialog-footer">
      <el-button @click="goBack">取 消</el-button>
      <el-button type="primary" @click="submitForm('params')">确 定</el-button>
    </div>

  </div>
</template>

<script>
import getAreaList from "common/components/plugin/searchArea/searchArea.vue";
import { mapActions, mapGetters } from "vuex";
export default {
  name: "customerinfoAdd",
  data() {
    return {
      InternalCompanyList: [],
      defaultArea: [], //默认的区域id
      cityList: [],
      is_service: 0,
      params: {
        is_disabled: 0,
        internal_company_id: 0,
        overdue_period: 0,
        company_id: "",
        company_name: "",
      },
      kuhulist: {
        options: [],
      },
      companyList: [],
      title: "新增",
      province: {
        options: [],
      },
      city: {
        options: [],
      },
      area: {
        options: [],
      },
      customerType: {
        options: [],
      },
      isShowDefaultArea: true,
      status: "add",
      employeeList: {
        options: [],
      },
      employee_name: localStorage.getItem("employee_name"),
    };
  },
  computed: {
    ...mapGetters(["customerinfoAdd_showGet"]),
  },
  components: {
    getAreaList, //区域列表
  },

  methods: {
    ...mapActions(["customerinfoIndexACT", "customerinfoSearch_show"]),
    getv2getInternalCompanyList() {
      this.$set(this, "InternalCompanyList", []);
      let params = {
        company_id: this.params.company_id,
        customer_id: this.customerinfoAdd_showGet.customer_id,
        type: 1, //1内部客户 2内部供应商
      };
      this.$axMethod.v2getInternalCompanyList(params).then((res) => {
        if (res.data.code == 200) {
          this.$set(this, "InternalCompanyList", res.data.data);
        }
      });
    },
    radioChange(val) {
      if (val != 2) {
        this.$refs["params"].clearValidate(["customer_mobile"]);
      }
    },
    customerNameBlur(val) {
      //客户名称
      if (val) {
        this.$set(this.params, "contact_name", val);
        this.$axios({
          url: this.$api.getJaneSpell,
          method: "get",
          params: {
            customer_name: val,
          },
        }).then((res) => {
          if (res.data.code == 200) {
            this.$set(this.params, "logogram", res.data.data);
          }
        });
      }
    },
    customerSearch(val) {
      //客户分类
      setTimeout(() => {
        // 防止快速输入
        this.$common.options("getCustomerTypeListOption", this.customerType, {
          company_id: this.params.company_id,
          customer_type_name: val,
        });
      }, 200);
      window.clearTimeout();
    },
    getCustomerTypeFoucs() {
      this.$common.options("getCustomerTypeListOption", this.customerType, {
        company_id: this.params.company_id,
      });
    },
    getbusinIdFoucs() {
      this.$common.options("getEmployeeListOption", this.employeeList, {
        company_id: this.params.company_id,
      }); //业务员
    },
    changeDefaultArea(addressId) {
      this.$set(this.params, "cityList", addressId.join(","));
      this.params.province_id = addressId[0];
      this.params.city_id = addressId[1];
      this.params.area_id = addressId[2];
    },
    goBack() {
      this.customerinfoIndexACT({
        // commont: "customerinfoDefault",
        params: this.customerinfoAdd_showGet.params,
        goPage: this.customerinfoAdd_showGet.goPage,
        pageSize: this.customerinfoAdd_showGet.pageSize,
      });
      this.$router.push({
        path: "/custom/customerinfo",
      });
    },
    business(result) {
      if (result) {
        this.is_service = Number(result.is_service);
        if (this.params.customer_genre == 2 && this.is_service !== 1) {
          this.params.customer_genre = 0;
        }
        this.params.company_name = result.company_name;
      }
      this.$set(this.params, "business_id", null);
      this.$set(this.params, "customer_type_id", null);
    },
    changeProvinceAdd() {
      this.params.city_id = "";
      this.$common.options("getAreaListOption", this.city, {
        level: 3,
        p_id: this.params.province_id,
      });
    },
    changeCityAdd() {
      this.$common.options("getAreaListOption", this.area, {
        level: 4,
        p_id: this.params.city_id,
      });
    },
    submitForm(params) {
      var vm = this;
      this.$refs[params].validate((valid) => {
        if (valid) {
          if (!vm.params.customer_id) {
            vm.$axios({
              method: "post",
              url: vm.$api.addCustomer,
              data: vm.params,
            }).then((res) => {
              if (res.data.code == 200) {
                this.$message({
                  message: res.data.msg,
                  type: "success",
                });
                this.goBack();
              }
            });
          } else {
            this.$axMethod.customEditCustomer(vm.params).then((res) => {
              if (res.data.code == 300) {
                this.$confirm(res.data.msg, {
                  confirmButtonText: "确定",
                  cancelButtonText: "取消",
                  type: "warning",
                }).then(() => {
                  this.$axMethod
                    .customEditCustomer(
                      Object.assign({}, vm.params, { can_disable: 1 })
                    )
                    .then(async (res) => {
                      if (res.data.code == 200) {
                        await this.$message({
                          message: res.data.msg,
                          type: "success",
                        });
                        this.goBack();
                      }
                    });
                });
              } else if (res.data.code == 200) {
                this.$message({
                  message: res.data.msg,
                  type: "success",
                });
                this.goBack();
              }
            });
          }
        }
      });
    },
    getCompanyList(list) {
      var obj = list.filter(
        (val) => val.company_id === Number(this.params.company_id)
      )[0];
      if (obj) {
        this.is_service = obj.is_service;
      }
    },
  },
  mounted() {
    if (this.customerinfoAdd_showGet.customer_id != undefined) {
      this.title = "修改";
      let vm = this;
      this.$axios({
        method: "get",
        url: this.$api.getCustomerInfo,
        params: {
          customer_id: this.customerinfoAdd_showGet.customer_id,
          associated_customer_id:
            this.customerinfoAdd_showGet.associated_customer_id,
        },
      }).then(({ data }) => {
        if (data.code == 200) {
          vm.params = data.data;
          vm.params.is_disabled = data.data.is_disabled;
          vm.params.customer_genre = data.data.customer_genre;

          let s = [];
          data.data.internal_company.forEach((element) => {
            s.push(element.company_id);
          });
          this.$set(this.params, "internal_company_id", s);
          this.$set(this, "InternalCompanyList", data.data.internal_company);

          this.defaultArea = [
            data.data.province_id,
            data.data.city_id,
            data.data.area_id,
          ];
          this.companyList = [
            {
              company_name: vm.params.company_name,
              company_id: vm.params.company_id,
            },
          ];
          this.customerType.options = [
            {
              customer_type_name: vm.params.customer_type_name,
              customer_type_id: vm.params.customer_type_id,
            },
          ];
          this.employeeList.options = [
            {
              employee_name: vm.params.business_name,
              employee_id: vm.params.business_id,
            },
          ];
          this.params.cityList = this.defaultArea.join(",");
          this.params.province_id = this.defaultArea[0];
          this.params.city_id = this.defaultArea[1];
          this.params.area_id = this.defaultArea[2];
          this.$refs.erpCompanyList.getList();
        }
      });
      this.$common.options("getAreaListOption", this.city, {
        level: 3,
        p_id: this.params.province_id,
      });
      this.$common.options("getAreaListOption", this.area, {
        level: 4,
        p_id: this.params.city_id,
      });
      this.isShowDefaultArea = false;
    } else {
      this.title = "新增";
      this.$set(this.params, "customer_genre", 0);
      this.$axios({
        url: this.$api.getSon2CompanyInfo,
        method: "get",
        params: {},
      }).then((res) => {
        if (res.data.code == 200) {
          this.defaultArea = [
            res.data.data.province,
            res.data.data.city,
            res.data.data.area,
          ];
          this.params.cityList = this.defaultArea.join(",");
          this.params.province_id = this.defaultArea[0];
          this.params.city_id = this.defaultArea[1];
          this.params.area_id = this.defaultArea[2];
        }
      });
      this.params.company_id = Number(localStorage.company_id);
      this.$refs.erpCompanyList.getList();
    }
  },
  beforeDestroy() {
    window.clearTimeout();
  },
};
</script>

<style lang="less">
