  border: 1px solid #ddd;
  padding: 20px 0;
}

.heightLabel .el-form-item__label {
  line-height: 50px !important;
}

.heightLabel .el-textarea__inner {
  resize: none !important;
  background: none !important;
  margin-left: 16px;
  color: #606266 !important;
  width: 301px;
}
.paymanageTitle {
  height: 37px;
  line-height: 37px;
  background: #eee;
  padding-left: 33px;
  position: relative;
  font-size: 14px;
  font-weight: bold;
  margin-bottom: 7px;
  color: #343434;
}
.specialBorderDiv {
  font-size: 14px;
  width: 95%;
  height: 120px;
  border: 1px dashed #000;
  margin: 5px 2%;
  p {
    color: red;
    font-weight: bold;
    margin: 10px 15px;
  }
  div {
    span {
      font-weight: bold;
    }
    margin-left: 14px;
    div {
      margin-left: 95px;
      margin-top: 5px;
    }
  }
}
</style>
<style lang="less">
.customerSelect .el-form-item__content .el-input {
  width: 300px;
}

.customerSelect .el-input--small .el-input__inner {
  width: 300px;
}
.customerSelect .el-form-item__content .el-input {
  width: 100%;
}
.customerInfoForm {
  .el-form-item__content .el-input {
    width: 300px;
  }

  .el-cascader__label {
    left: 5%;
